/**  Store */
export const Satisfaction = {
  namespaced: true,
  state: () => ({
    /**    */
    code: [],
  }),
  mutations: {
    SET_CODE(state, code) {
      state.code = code;
    },
  },
  actions: {
    FETCH_CODE({ commit }, values) {
      commit('SET_CODE', values);
    },
  },
};
